import React, { Component } from 'react'
import style from './style.module.css';
import data from './detail.json';

class Setting extends Component {       
    constructor(props) {
        super(props);
        this.state = {
            session:true,
        }
    }

    render() {
        return (
            <div className={style.sidePane} >
                <div className="m-4">
                    <img src={data.profile} className={style.avatar} alt={data.userName} />
                    <br/><br/>
                    <h3>Welcome, {data.fName} {data.lName}</h3>
                    <button onClick={this.props.logout}>Logout...</button>
                </div>
            </div>
        )
    }
}

export default Setting;