import React, { Component } from 'react'
import List from './List';
import Setting from './Setting';
import Login from './Login';

class Welcome extends Component {
    constructor(props){
        super(props);
        this.state={
            items: "",
            logout: false
        }
        
        this.handleLogout = this.handleLogout.bind(this);
    }

    handleLogout(){
        this.setState({logout:true});
    }

    componentDidMount(){
        fetch("https://api.github.com/users")
        .then(res => res.json())
        .then(
            (result) => {
            this.setState({
                items: result
            });
            })
    }
    render() {
        return (
            <div>
                {(this.state.logout) ? <Login /> : null}
                <div className="row m-0 p-0" style={{display:(this.state.logout) ? "none":null}}>
                    <div className="col-2 bg-info">
                        {(this.state.items)?  <Setting logout={this.handleLogout} items={this.state.items} />: "Loading..."}
                    </div>
                    <div className="col-10">
                        {(this.state.items)?  <List items={this.state.items} />: "Loading..."}
                    </div>
                </div>
            </div>
        )
    }
}

export default Welcome;
