import React, { Component } from 'react'
import Welcome from './Welcome';
import data from './detail.json';
import style from './style.module.css';

class Login extends Component {
    constructor(props){
        super(props);
        this.state={
            uname : data.userName,
            pwd: data.password,
            realUser:false,
            info:"",
            loginView: "d-block"
        }
        this.handleLogin = this.handleLogin.bind(this);
    }

    handleLogin(){
        let uName= document.getElementById('uname').value;
        let password= document.getElementById('upwd').value;
        
        if(uName === this.state.uname && password === this.state.pwd){
            this.setState({
                realUser:true,
                loginView: "d-none"
            });
        } else{
            alert("Wrong Credentials");
        }
    }

    render() {
        return (
            <div>
                {
                (this.state.realUser)
                ?
                <Welcome logout={this.handleLogout} />
                :
                <div className={this.state.loginView}>
                    <div className={style.login}>
                        <label>User Name</label>
                        <input type="text" id="uname" placeholder="User Name : admin"/>
                        <br/>
                        <label>Password</label>
                        <input type="password" id="upwd"  placeholder="Password : admin"/>
                        <br/>      
                        <button type="button" onClick={this.handleLogin}>Login</button>
                    </div>
                </div>
                }  
            </div>
        )
    }
}
export default Login; 