import React, { Component } from 'react'
import style from './style.module.css';

class List extends Component {       
    constructor(props) {
        super(props);
        this.state = {
            detail: this.props.items,
            ready:false,
            visible:'d-none'
        }
        this.showMore = this.showMore.bind(this);
    }

    showMore(e){
       this.setState({visible:"d-block"})
    }

    render() {
        return (
            <div className={style.row}>
                <div className={style.column}>
                    {this.state.detail.map((user,index) => (
                        <div className={style.card} key={index}>
                            <img src={user.avatar_url} className={style.avatar} alt={user.avatar_url} />
                            <p>ID: {user.id}</p>
                            <p>Name: {user.login}</p>
                            <p>Type: {user.type}</p>
                        </div>
                    ))}
                </div>
            </div>
        )
    }
}

export default List;